import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import { TranslateService } from '@ngx-translate/core';
import { MessageService } from 'primeng/api';
import { AutoCompleteCompleteEvent } from 'primeng/autocomplete';

import { ITagSearch } from '@ApiClient/backend/Extra/tag';

import { AppStateInterface } from '@store/app/app-state.interface';
import { addTagAction, clearTagsAction, postNewPostAction, removeTagAction } from '@store/newPost/newPost.actions';
import { selectNewPostTags } from '@store/newPost/newPost.selectors';
import { TagsNameService } from '@services/tags-name.service';
import { readFileAsBase64 } from '@utils/read-file-as-base-64';
import { TagsValidator } from '@utils/tags-validator';
import { PostData } from '@ApiClient/backend/Post/post';
import { LangCode } from '@ApiClient/backend/Common/language';

@Component({
  selector: 'app-new-post',
  templateUrl: 'new-post.component.html',
  styleUrls: ['new-post.component.scss'],
})
export class NewPostComponent implements OnInit {
  @ViewChild('fileInput') fileInput!: ElementRef;
  isDragover: boolean = false;
  maxLengthDescription: number = 5000;
  filesToSend: string[] = [];
  tagList: ITagSearch[] = [];
  filteredTagList: ITagSearch[] = [];
  tagsLength: number = 0;
  tags$ = this.store.select(selectNewPostTags);

  constructor(
    private store: Store<AppStateInterface>,
    private fb: FormBuilder,
    private translate: TranslateService,
    private toast: MessageService,
    private tagService: TagsNameService,
  ) {}

  async ngOnInit() {
    this.tags$.subscribe((tags) => {
      this.tagsLength = tags.length;
    });

    this.tagService.getTags().then((tags) => {
      this.tagList = tags;
    });
  }

  newPostForm = this.fb.group({
    title: new FormControl('', [Validators.required]),
    description: new FormControl('', [Validators.required, Validators.maxLength(this.maxLengthDescription)]),
    tags: new FormControl('', [TagsValidator.pattern(/^[a-zA-Z0-9]+$/)]),
  });

  onSubmit() {
    if (this.newPostForm.invalid) {
      this.newPostForm.markAllAsTouched();
      return;
    }

    const newPost: PostData = {
      files: this.filesToSend.map((file) => ({
        dataBase64: file,
        fileExt: 'png',
        thumbnail: {
          fileExt: 'png',
          dataBase64: file,
        },
      })),
      languages: [
        {
          title: this.newPostForm.get('title')?.value ?? '',
          description: this.newPostForm.get('description')?.value ?? '',
          langCode: this.translate.currentLang as keyof typeof LangCode,
        },
      ],
    };

    console.log(newPost);
    this.store.dispatch(postNewPostAction({ newPost }));
  }

  async handleFilesUpload(event: Event | DragEvent) {
    let fileList: FileList | null = null;
    if (event instanceof Event) {
      const target = event.target as HTMLInputElement;
      fileList = target.files;
    }
    if (event instanceof DragEvent) {
      this.isDragover = false;
      fileList = event.dataTransfer ? event.dataTransfer.files : null;
    }

    if (fileList !== null && fileList.length > 0) {
      const files: File[] = Array.from(fileList);
      const base64Strings: string[] = [];
      for (const file of files) {
        if (!file.type.startsWith('image/')) {
          this.showErrorMessage('ERROR.WRONG_TYPE');
          return;
        } else {
          const fileBase64string = await readFileAsBase64(file);
          base64Strings.push(fileBase64string);
        }
      }
      this.filesToSend = base64Strings;
    }
  }

  clickFileInput() {
    this.filesToSend = [];
    this.fileInput.nativeElement.click();
  }

  filterAutoCompleteTags(event: AutoCompleteCompleteEvent) {
    const query = event.query.toLowerCase();
    this.filteredTagList = this.tagList.filter((tag) => {
      const tagName = tag.name.toLowerCase();
      return tagName.startsWith(query);
    });
  }

  onKeyDown(event: KeyboardEvent) {
    if ((event.key === ' ' || event.key === 'Enter') && !this.newPostForm.get('tags')?.hasError('pattern')) {
      const inputValue = (event.target as HTMLInputElement).value.trim();
      if (inputValue !== '') {
        this.store.dispatch(addTagAction({ newTag: inputValue }));
        this.filteredTagList = [];
        (event.target as HTMLInputElement).value = '';
      }
      event.preventDefault();
    }
  }

  removeTag(tag: string) {
    this.store.dispatch(removeTagAction({ tagToRemove: tag }));
  }

  clearTags() {
    this.store.dispatch(clearTagsAction());
  }

  private showErrorMessage(key: string): void {
    this.toast.add({
      severity: 'error',
      summary: this.translate.instant('ERROR.TITLE'),
      detail: this.translate.instant(key),
    });
  }
}
