import { AppStateInterface } from '@store/app/app-state.interface';
import { createSelector } from '@ngrx/store';
import { NewPostStateInterface } from '@store/newPost/newPostState.interface';

export const newPostFeatureSelector = (state: AppStateInterface): NewPostStateInterface => state.newPost;

export const selectIsLoadingNewPost = createSelector(newPostFeatureSelector, (state: NewPostStateInterface) => state.isLoading);

export const selectThumbnailNumber = createSelector(newPostFeatureSelector, (state: NewPostStateInterface) => state.imageToThumbnail);

export const selectNewPostTags = createSelector(newPostFeatureSelector, (state: NewPostStateInterface) => state.tags);
